import React from 'react';
import './configured-oidc-idp.scss'
import {oidcResponseData} from "../CommonInterfaces/idp-response-types";
import {
    ActionList,
    ActionListItem,
    Button,
    ClipboardCopy,
    Form,
    FormGroup,
    FormHelperText,
    HelperText,
    HelperTextItem,
    Popover,
    Stack,
    StackItem,
    Text,
    TextContent,
    TextVariants,
    Title
} from '@patternfly/react-core';
import HelpIcon from '@patternfly/react-icons/dist/esm/icons/help-icon';
import styles from '@patternfly/react-styles/css/components/Form/form';
import {useChrome} from "@redhat-cloud-services/frontend-components/useChrome";
import {getSSOBaseUrl} from "../../../Utils/general-utils";

interface Props {oidcData: oidcResponseData}

const ConfiguredOidcIdp = ({oidcData}: Props) => {
    const chrome = useChrome();
    const handleConfigUrl = (path: string) => {
        const configUrl = getSSOBaseUrl(chrome) + path;
        window.open(configUrl, "_blank");
    };

    return (
        <Form>
            <Title headingLevel={"h1"}>{oidcData.displayName}</Title>

            <FormGroup
                label="Issuer"
                fieldId="oidc-issuer"
                labelIcon={
                    <Popover
                        bodyContent={
                            <div>
                                The issuer is used to validate tokens generated by the identity provider and allows additional security for your organization. This value is optional but recommended.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={styles.formGroupLabelHelp}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <ClipboardCopy
                    type="text"
                    id="oidc-issuer-clipboard"
                    name="oidc-issuer-clipboard"
                    isReadOnly>
                    {oidcData.issuerUrl}
                </ClipboardCopy>
            </FormGroup>

            <FormGroup
                label="Client ID"
                fieldId="oidc-client-id"
                labelIcon={
                    <Popover
                        bodyContent={
                            <div>
                                The client ID registered with the identity provider.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={styles.formGroupLabelHelp}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <ClipboardCopy
                    type="text"
                    id="oidc-client-id-clipboard"
                    name="oidc-client-id-clipboard"
                    isReadOnly>
                    {oidcData.clientId}
                </ClipboardCopy>
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>Spaces and special characters are allowed here.</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Client Secret"
                fieldId="oidc-client-secret"
                labelIcon={
                    <Popover
                        bodyContent={
                            <div>
                                The client secret registered with the identity provider.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={styles.formGroupLabelHelp}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <ClipboardCopy
                    type="password"
                    id="oidc-client-id-clipboard"
                    name="oidc-client-id-clipboard"
                    isReadOnly>
                    {oidcData.clientSecret}
                </ClipboardCopy>
            </FormGroup>

            <FormGroup
                label="Authorization URL"
                fieldId="oidc-authorization-url-id"
                labelIcon={
                    <Popover
                        bodyContent={
                            <div>
                                URL that performs authentication of the end-user.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={styles.formGroupLabelHelp}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <ClipboardCopy
                    type="text"
                    id="oidc-authorization-url-clipboard"
                    name="oidc-authorization-url-clipboard"
                    isReadOnly>
                    {oidcData.authorizationUrl}
                </ClipboardCopy>
            </FormGroup>

            <FormGroup
                label="Token URL"
                fieldId="oidc-token-url-id"
                labelIcon={
                    <Popover
                        bodyContent={
                            <div>
                                URL where an authorization code can be exchanged for an access token and ID token.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={styles.formGroupLabelHelp}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <ClipboardCopy
                    type="text"
                    id="oidc-token-url-clipboard"
                    name="oidc-token-url-clipboard"
                    isReadOnly>
                    {oidcData.tokenUrl}
                </ClipboardCopy>
            </FormGroup>

            <Title headingLevel={"h3"}>URLs required for identity provider configuration</Title>

            <TextContent>
                <Text component={TextVariants.p}>
                    Use these URLs to assist in completing setup within your identity provider.
                </Text>
                <Text className="pf-v5-u-active-color-100 customUrl" component={TextVariants.p} onClick={() => handleConfigUrl('.well-known/openid-configuration')}>
                    Service provider metadata URL
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                Service provider metadata used to integrate sso.redhat.com with your identity provider.
                            </div>
                        }
                    >
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={styles.formGroupLabelHelp}
                        >
                            <HelpIcon />
                        </Button>
                    </Popover>
                </Text>
                <Text className="pf-v5-u-active-color-100 customUrl" component={TextVariants.p} onClick={() => handleConfigUrl(`broker/${oidcData.idpAlias}/endpoint`)}>
                    Redirect URL / Assertion Consumption Service (ACS) URL
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                The redirect URL to use when configuring your identity provider. This represents your companys SSO Portal where users authenticate to Red Hat.
                            </div>
                        }
                    >
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={styles.formGroupLabelHelp}
                        >
                            <HelpIcon />
                        </Button>
                    </Popover>
                </Text>
            </TextContent>

            <ActionList>
                <Popover
                    triggerAction="hover"
                    aria-label="Hoverable popover"
                    headerContent={<div>Read-Only</div>}
                    bodyContent={<div>This page currently shows a read-only version of your configuration. Forthcoming enhancements will provide the ability to edit.</div>}
                    footerContent="">
                    <ActionListItem>
                        <Button isDisabled>
                            Save
                        </Button>
                    </ActionListItem>
                </Popover>
                <ActionListItem>
                    <Button isDisabled variant="link">
                        Cancel
                    </Button>
                </ActionListItem>
            </ActionList>

        </Form>
    )
}

export default ConfiguredOidcIdp;
