import './identity-provider-integration.scss';
import React, { useState, useEffect } from 'react';
import FlowTemplate from '../../Apps/IdpSelfService/IdpFlowTemplate/flow-template'
import DisabledPage from "../../Components/DisabledPage/disabled-page";
import {isAdminUser} from '../../Utils/general-utils';
import {useFlag} from "@unleash/proxy-client-react";
import {useChrome} from '@redhat-cloud-services/frontend-components/useChrome';


const IdentityProviderIntegration = () => {
    const chrome = useChrome();
    let userToken = null;
    const [currentUser, updateUser] = useState({
        adminUser: false,
        selfServiceIDP: <DisabledPage/>
    })
    const isSelfServiceIDPEnabled = useFlag('iamconsole.IDPIntegration.enabled');

    useEffect(() => {
        // eslint-disable-next-line no-undef
        chrome?.appAction?.('identity-provider-integration');
        // eslint-disable-next-line no-undef
        chrome.auth.getToken().then(token => {
            userToken = token;
            // if the user is in fact and admin user then render TwoFactorCheckOld
            if (isAdminUser(userToken!) && isSelfServiceIDPEnabled) {
                updateUser({
                    adminUser: true,
                    selfServiceIDP: <FlowTemplate accessToken={userToken!} />
                })
            }
        })
    }, []);

    return (
        <React.Fragment>
            {currentUser.selfServiceIDP}
        </React.Fragment>
    )

}

export default IdentityProviderIntegration
