// step three, choice 3 of the flow https://miro.com/app/board/uXjVO_x__QE=/

import {
    FileUpload,
    Form,
    FormGroup,
    Title,
    Text,
} from '@patternfly/react-core';
import React, {useState} from 'react';
import xml2js from 'xml2js';
import PropTypes from "prop-types";
import {InputValidation} from "../CommonInterfaces/input-validation";
import {UserChoices} from "../CommonInterfaces/user-choices";

interface Props {
    updateStatus: Function;
    resetNextClicked: Function;
    rootValidation: React.MutableRefObject<InputValidation>;
    rootUserChoices: React.MutableRefObject<UserChoices>;
}

const ImportFromFile = ({updateStatus, rootValidation, rootUserChoices}: Props) => {

    const [fileData, setFileData] = useState("");


    /**
     *
     */
    const handleFileData = (_event: any, data:string) => {
        setFileData(data);
        xml2js.parseString(data, (err, result) => {
            if (err) {
                console.error(err);
            }
            else if (result) {
                updateStatus("FINAL_STEP");
                rootUserChoices.current.current.configFileData = result;

                /*
                 * may need to submit a query at this point to the api to validate that the file chosen is good to go
                 * alternatively a validation step could be added locally to check for the existence of required fields
                 */
                rootValidation.current.IMPORT_FROM_FILE = true;
            }
        })
    }


    return <Form>
                <Title headingLevel="h2">Import from file</Title>
                <FormGroup label="File Upload" isRequired >
                    <FileUpload
                        value={fileData.toString()}
                        id="text-file-with-edits-allowed"
                        type="text"
                        onDataChange={handleFileData}
                        browseButtonText="Upload"
                    />
                </FormGroup>
                <Text className="pf-u-color-400 pf-u-font-size-sm">An XML formatted file detailing the configuration of your IDP, minimally including the OpenID Connect Identity provider discovery URL</Text>
            </Form>

}

ImportFromFile.propTypes = {
    rootUserChoices: PropTypes.object,
    rootValidation: PropTypes.object,
    updateStatus: PropTypes.func
}

export default ImportFromFile;
