// IDP test instructions

import {
    Text,
    Form,
    List,
    ListItem,
    Button,
    Title
} from '@patternfly/react-core';
import React, {useEffect} from 'react';
import PropTypes from 'prop-types';

interface Props{
    updateStatus: Function;
}

const TestProvider = ({updateStatus}: Props) => {

    useEffect(() => {
        updateStatus("VALIDATESTEP");
    }, [])


    return <Form>
        <Title headingLevel="h2">Test your identity provider</Title>
        <Text className="pf-u-color-400 pf-u-font-size-sm">Test your identity provider integration before enabling it for your entire organization</Text>
        <List>
            <ListItem>Click the button to initiate login redirect to your identity provider</ListItem>
            <ListItem>Log in with your identity provider user your company Single Sign-on credentials</ListItem>
            <ListItem>You will be redirected back to identity provider integration configuration where you can then enable it for your entire organization</ListItem>
        </List>

        <Text>If you encounter any errors or are having trouble, contact
            <Button variant="link" href={"www.google.com"} target="_blank" component="a" isInline className="pf-u-ml-xs">
            CustomerService
            </Button>
        </Text>
    </Form>

}

TestProvider.propTypes = {
    updateStatus: PropTypes.func
}

export default TestProvider;
