// Final page to display the urls of the flow https://miro.com/app/board/uXjVO_x__QE=/

import {
    Form,
    FormGroup,
    Title,
    Text,
    ClipboardCopy
} from '@patternfly/react-core';
import React, {useEffect} from 'react';
import PropTypes from 'prop-types';


interface Props{
    updateStatus: Function;
    redirectDomain: string | undefined;
}

const ImportSamlFromURL = ({updateStatus, redirectDomain} : Props) => {

    useEffect(() => {
        updateStatus("VALIDATE_STEP");
    }, [])

    return <Form>
                <Title headingLevel="h2">Configure URLs</Title>
                <Text className="pf-u-color-400 pf-u-font-size-sm">Some Identity Provider Services require additional
                    configuration when enabling third party single sign on. Please review the following information and
                    verify that these are filled in where required in your company IDP.</Text>
                <FormGroup label="Identifier (Entity ID)">
                    <ClipboardCopy isReadOnly clickTip="copied">
                        https://sso.redhat.com/auth/realms/redhat-external
                    </ClipboardCopy>
                </FormGroup>

                <FormGroup label="Sign on URL">
                    <ClipboardCopy isReadOnly clickTip="copied">
                        https://sso.redhat.com/auth/realms/redhat-external/protocol/saml
                    </ClipboardCopy>
                </FormGroup>

                <FormGroup label="Redirect URL or ACS (Assertion Consumption Service) URL">
                    <ClipboardCopy isReadOnly clickTip="copied">
                        {"https://sso.redhat.com/auth/realms/redhat-external/broker/" + { redirectDomain } + "/endpoint"}
                    </ClipboardCopy>
                </FormGroup>

            </Form>

}

ImportSamlFromURL.propTypes = {
    updateStatus: PropTypes.func,
    redirectDomain: PropTypes.string
}

export default ImportSamlFromURL;
