import {
    Radio,
    Form,
    FormGroup
} from '@patternfly/react-core';

import React, {useState} from 'react';
// eslint-disable-next-line no-unused-vars
import {setStateValue, processAllInputs} from '../../../Utils/general-utils';
import PropTypes from "prop-types";
import {InputValidation} from "../CommonInterfaces/input-validation";
import {UserChoices} from "../CommonInterfaces/user-choices";
import { ValidationTemplate} from "../CommonInterfaces/oidc-validation";

interface Props{
    updateStatus: Function;
    resetNextClicked: Function;
    rootValidation: React.MutableRefObject<InputValidation>;
    rootUserChoices: React.MutableRefObject<UserChoices>;
}

const SamlConfiguration = ({updateStatus, resetNextClicked, rootValidation, rootUserChoices}: Props) => {


    const [validations, setValidations] = useState<ValidationTemplate>({
        'inputMethod': {
            inputType: 'RADIO',
            status: 'default',
            message: ''
        }
    });

    const handleSelection = (value: any) => {

        /*
         * regardless of current state. if the user alters the input clear existing flags. This should also reset
         * the currently displayed errors
         */
        resetNextClicked(false);

        //  update the main data storage object with the new value
        rootUserChoices.current.selections[value.target.id] = value.target.value;

        /*
         * update the validation settings. It is expected that the id's used in the validation object matches
         * the id's used on the elements.
         */
        const failureTriggered = processAllInputs(rootUserChoices.current.selections, validations, setValidations);

        rootValidation.current.SAML_CONFIG = !failureTriggered;

        if (!failureTriggered && rootUserChoices.current.selections.inputMethod) {
            updateStatus(rootUserChoices.current.selections.inputMethod);
        }
    }

    return <Form>

                <FormGroup
                    onChange={(value) => { handleSelection(value) }}
                    label="Load external IDP metadata from a config file or download it from a url"
                    isRequired
                >
                    <Radio name="inputChoice" id="inputMethod" label="Import from URL" value="IMPORT_SAML_FROM_URL"/>
                    <Radio name="inputChoice" id="inputMethod" label="Import from file" value="IMPORT_SAML_FROM_FILE"/>
                </FormGroup>
            </Form>

}

SamlConfiguration.propTypes = {
    resetNextClicked: PropTypes.func,
    rootUserChoices: PropTypes.object,
    rootValidation: PropTypes.object,
    nextClicked: PropTypes.func,
    updateStatus: PropTypes.func
}

export default SamlConfiguration;
