// step two, choice 1 of the flow https://miro.com/app/board/uXjVO_x__QE=/
import React, {useState} from 'react';
import { 
    Text,
    TextInput,
    Radio,
    Form,
    FormGroup,
    HelperText,
    HelperTextItem
} from '@patternfly/react-core';
import {processAllInputs, setStateValue} from '../../../Utils/general-utils';
import PropTypes from 'prop-types';
import {UserChoices} from '../CommonInterfaces/user-choices'
import {InputValidation} from '../CommonInterfaces/input-validation'
import {ValidationTemplate} from "../CommonInterfaces/oidc-validation";

interface Props{
    updateStatus: Function;
    resetNextClicked: Function;
    rootValidation: React.MutableRefObject<InputValidation>;
    rootUserChoices: React.MutableRefObject<UserChoices>;
}

const OidcConfiguration = ({updateStatus, resetNextClicked, rootValidation, rootUserChoices}: Props) => {

    const [selections, setSelections] = useState<Object>();
    const [validations, setValidations] = useState<ValidationTemplate>({
        'clientId': {
            inputType: 'GENERAL',
            status: 'default',
            message: ''
        },
        'clientSecret': {
            inputType: 'GENERAL',
            status: 'default',
            message: ''
        },
        'inputMethod': {
            inputType: 'RADIO',
            status: 'default',
            message: ''
        }
    });


    /**
     * handle selection needs to update information being used to govern
     * the applicaton flow as well as the final api update object
     */
    const handleSelection = (value: any) => {

        /*
         * regardless of current state. if the user alters the input clear existing flags. This should also reset
         * the currently displayed errors
         */
        resetNextClicked(false);
        //  update the main data storage object with the new value
        rootUserChoices.current.selections[value.target.id] = value.target.value;

        /*
         * update the validation settings. It is expected that the id's used in the validation object matches
         * the id's used on the elements.
         */
        const failureTriggered = processAllInputs(rootUserChoices.current.selections, validations, setValidations);

        rootValidation.current.OIDC_CONFIG = !failureTriggered;

        if (!failureTriggered && rootUserChoices.current.selections.inputMethod) {
            updateStatus(rootUserChoices.current.selections.inputMethod);
        }

    }

    // @ts-ignore
    return <Form>
                <Text className="pf-u-color-400 pf-u-font-size-sm">All fields are required</Text>
                <FormGroup
                    label="Client ID/SP entity ID"
                    onChange={(value) => { handleSelection(value) }}
                >
                    <TextInput
                        onChange={(_event, value) => setStateValue(value, "clientId", selections, setSelections)}
                        type="text"
                        id="clientId"
                    />
                    <HelperText>
                        <HelperTextItem>
                            The Red Hat client identifier registered within the customer identity provider. Your indentity provider generates this client ID when a Red Hat client is added
                        </HelperTextItem>
                    </HelperText>
                </FormGroup>

                <FormGroup
                    label="Client Secret"
                    onChange={(value) => { handleSelection(value) }}>
                    <TextInput
                        onChange={(_event, value) => setStateValue(value, "clientSecret", selections, setSelections)}
                        type="text"
                        id="clientSecret"
                    />
                    <HelperText>
                        <HelperTextItem>
                            Enter the client secret associated with your client identifier  by your identity provider
                        </HelperTextItem>
                    </HelperText>
                </FormGroup>

                <FormGroup
                    onChange={(value) => { handleSelection(value) }}
                    label="Load external IDP metadata from a config file or download it from a url"
                >
                    <Radio name="inputChoice" id="inputMethod" label="Import from URL" value="IMPORT_FROM_URL"/>
                    <Radio name="inputChoice" id="inputMethod" label="Import from file" value="IMPORT_FROM_FILE"/>
                </FormGroup>

                
            </Form>

}

OidcConfiguration.propTypes = {
    resetNextClicked: PropTypes.func,
    rootUserChoices: PropTypes.object,
    rootValidation: PropTypes.object,
    nextClicked: PropTypes.func,
    updateStatus: PropTypes.func
}

export default OidcConfiguration;
