import React from 'react';
import './configured-saml-idp.scss'
import {samlResponseData} from "../CommonInterfaces/idp-response-types";
import {
    ActionList,
    ActionListItem,
    Button,
    ClipboardCopy,
    ClipboardCopyVariant,
    Form,
    FormGroup,
    FormHelperText,
    HelperText,
    HelperTextItem,
    Popover,
    Stack,
    StackItem,
    Text,
    TextContent,
    TextVariants,
    Title
} from '@patternfly/react-core';
import HelpIcon from '@patternfly/react-icons/dist/esm/icons/help-icon';
import styles from '@patternfly/react-styles/css/components/Form/form';
import {useChrome} from "@redhat-cloud-services/frontend-components/useChrome";
import {getSSOBaseUrl} from "../../../Utils/general-utils";

interface Props {
    samlData: samlResponseData
}

const ConfiguredSamlIdp = ({samlData}: Props) => {
    const chrome = useChrome();
    const handleConfigUrl = (path: string) => {
        const configUrl = getSSOBaseUrl(chrome) + path;
        window.open(configUrl, "_blank");
    };

    return (
        <Form>
            <Title headingLevel={"h1"}>{samlData.displayName}</Title>

            <FormGroup
                label="Identity provider entityID"
                fieldId="saml-entity-id"
                labelIcon={
                    <Popover
                        bodyContent={
                            <div>
                                The entityID attribute can be found in your SAML metadata configuration
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={styles.formGroupLabelHelp}>
                            <HelpIcon/>
                        </Button>
                    </Popover>
                }>
                <ClipboardCopy
                    type="text"
                    id="saml-entity-id-clipboard"
                    name="saml-entity-id-clipboard"
                    isReadOnly>
                    {samlData.identityProviderEntityId}
                </ClipboardCopy>
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>The entityID used to validate the Issuer for received SAML
                            assertions.</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Single Sign-on authentication request URL"
                fieldId="saml-authn-request-url">
                <ClipboardCopy
                    type="text"
                    id="saml-authn-request-url-clipboard"
                    name="saml-authn-request-url-clipboard"
                    isReadOnly>
                    {samlData.authenticationRequestsUrl}
                </ClipboardCopy>
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>The URL that will be used to send SAML AuthN Requests.</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="x509 certificate"
                fieldId="saml-x509-certificate"
                labelIcon={
                    <Popover
                        bodyContent={
                            <div>
                                A PEM certificate that is used to check for signatures.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={styles.formGroupLabelHelp}>
                            <HelpIcon/>
                        </Button>
                    </Popover>
                }>
                <ClipboardCopy
                    id="saml-x509-certificate-clipboard"
                    name="saml-x509-certificate-clipboard"
                    variant={ClipboardCopyVariant.expansion}
                    isReadOnly
                    isExpanded>
                    {samlData.x509Certificate}
                </ClipboardCopy>
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>
                            A Base64 encoded certificate must not include the certificate preamble/ending
                            <br/>
                            (-----BEGIN/END CERTIFICATE-----)
                        </HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <Title headingLevel={"h3"}>URLs required for identity provider configuration</Title>

            <TextContent>
                <Text component={TextVariants.p}>
                    Use these URLs to assist in completing setup within your identity provider.
                </Text>
                <Text className="pf-v5-u-active-color-100 customUrl" component={TextVariants.p}
                      onClick={() => handleConfigUrl(`broker/${samlData.idpAlias}/endpoint/descriptor`)}>
                    Service provider metadata URL
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                Service provider metadata used to integrate sso.redhat.com with your identity provider.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={styles.formGroupLabelHelp}>
                            <HelpIcon/>
                        </Button>
                    </Popover>
                </Text>
                <Text className="pf-v5-u-active-color-100 customUrl" component={TextVariants.p}
                      onClick={() => handleConfigUrl(`broker/${samlData.idpAlias}/endpoint`)}>
                    Redirect URL / Assertion Consumption Service (ACS) URL
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                The redirect URL to use when configuring your identity provider. This represents your
                                companys SSO Portal where users authenticate to Red Hat.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={styles.formGroupLabelHelp}>
                            <HelpIcon/>
                        </Button>
                    </Popover>
                </Text>
            </TextContent>

            <ActionList>
                <Popover
                    triggerAction="hover"
                    aria-label="Hoverable popover"
                    headerContent={<div>Read-Only</div>}
                    bodyContent={<div>This page currently shows a read-only version of your configuration. Forthcoming
                        enhancements will provide the ability to edit.</div>}
                    footerContent="">
                    <ActionListItem>
                        <Button isDisabled>
                            Save
                        </Button>
                    </ActionListItem>
                </Popover>
                <ActionListItem>
                    <Button isDisabled variant="link">
                        Cancel
                    </Button>
                </ActionListItem>
            </ActionList>

        </Form>
    )
}

export default ConfiguredSamlIdp;
